@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}
@media (max-width: 1199px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.container--header {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 0;
}
@media (max-width: 1199px) {
  .container--header {
    margin-bottom: 17px;
    padding-top: 36px;
    align-items: flex-end;
  }
}
@media (max-width: 767px) {
  .container--header {
    padding-top: 15px;
    align-items: flex-start;
  }
}

.btn {
  display: inline-flex;
  padding: 20px;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  transition: opacity 0.3s ease;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  justify-content: center;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  background: conic-gradient(from 160deg at 16.2% 39.5%, #003d85 8.95%, #b80027 79%, #003d85);
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header {
    background: conic-gradient(from 160deg at 21% 36%, #003d85 6.95%, #b80027 79%, #003d85);
  }
}
@media (max-width: 767px) {
  .header {
    background: conic-gradient(from 202deg at 35% 34%, #003d85 1.95%, #b80027 79%, #003d85);
  }
}

@media (max-width: 1199px) {
  .header__logo-image {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo-image {
    width: 69px;
    height: 24px;
  }
}

.header__phone {
  font-family: "Alumni Sans", "Arial Narrow", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 1.2;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .header__phone {
    font-size: 24px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__phone {
    font-size: 20px;
  }
}

.main-screen {
  display: grid;
  grid-template-columns: 132px 265px auto;
  grid-template-areas: "list city img" "list header img" "list button img";
  justify-items: end;
  margin-bottom: 21px;
}
@media (max-width: 1199px) {
  .main-screen {
    grid-template-columns: 17% 31% auto;
    margin-bottom: 74px;
  }
}
@media (max-width: 767px) {
  .main-screen {
    grid-template-columns: 39% 61%;
    grid-template-areas: "list city" "list header" "button button";
    margin-bottom: 49px;
  }
}

.main-screen__header {
  grid-area: header;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Alumni Sans", "Arial Narrow", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-align: right;
  text-transform: uppercase;
  -webkit-text-stroke: 4px;
}
@media (max-width: 1199px) {
  .main-screen__header {
    font-size: 72px;
    text-transform: none;
  }
}

.main-screen__city {
  grid-area: city;
  margin-bottom: 0;
  margin-top: 110px;
  margin-right: 54px;
  line-height: 0;
  font-family: "Alumni Sans", "Arial Narrow", sans-serif;
  font-weight: 600;
  font-size: 48px;
  text-transform: lowercase;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .main-screen__city {
    margin-top: 69px;
    margin-bottom: 30px;
    margin-right: 6px;
    font-size: 34px;
  }
}
@media (max-width: 767px) {
  .main-screen__city {
    margin-top: 62px;
    margin-right: 4px;
    font-size: 28px;
  }
}

.main-screen__list {
  grid-area: list;
  margin-top: 55px;
  padding: 0;
  list-style: none;
  font-size: 14px;
  line-height: 1.45;
  text-align: right;
  align-self: center;
  grid-row: 2/3;
}
@media (max-width: 1199px) {
  .main-screen__list {
    margin-top: 81px;
  }
}
@media (max-width: 767px) {
  .main-screen__list {
    margin-top: 62px;
  }
}

.main-screen__link {
  grid-area: button;
  margin-top: 3px;
  margin-bottom: 30px;
  margin-left: 31px;
  align-self: flex-start;
  justify-self: stretch;
}
@media (max-width: 1199px) {
  .main-screen__link {
    margin-top: 34px;
    margin-left: 26px;
  }
}
@media (max-width: 767px) {
  .main-screen__link {
    margin-top: 22px;
    margin-left: 0;
  }
}

.main-screen__img {
  grid-area: img;
  position: relative;
  top: -30px;
  margin-left: 62px;
  justify-self: start;
}
@media (max-width: 1199px) {
  .main-screen__img {
    width: 324px;
    height: 390px;
    margin-left: 30px;
    position: static;
    object-fit: cover;
    justify-self: center;
  }
}
@media (max-width: 767px) {
  .main-screen__img {
    display: none;
  }
}

.main-screen picture {
  grid-area: img;
  justify-self: start;
}
@media (max-width: 1199px) {
  .main-screen picture {
    justify-self: center;
  }
}

